const mainState={
    isMenu:null
}

const MainReducer =(state=mainState,action)=>{
    switch(action.type){
        case 'MENU':
            return{
                ...state,
                isMenu:!state.isMenu
            }
        case 'SPREAD_MENU':
            return{
                ...state,
                isMenu: action.isSpread
            }

        default: return state
    }
}

export default MainReducer;