import React,{Component} from 'react';
import {BrowserRouter as Router} from 'react-router-dom';
import './SCSS/App.css';
import Header from './MainComponents/Header';
import MainContent from './MainComponents/MainContent';
import { connect } from 'react-redux';

let listenerWidth = null

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {  }
    this.handleWidth = this.handleWidth.bind(this);
  }

  handleWidth(){
      let width = window.innerWidth;
      if(width > 768){
        this.props.setWidth(true)
      }else{
        this.props.setWidth(false)
      }
  }

  componentDidMount(){
      this.handleWidth()
      listenerWidth = window.addEventListener('resize', this.handleWidth)
  }

  componentWillUnmount(){
    listenerWidth = window.removeEventListener('resize', this.handleWidth)
  }

  render() { 
    return( 
      <>
        <Router basename={process.env.PUBLIC_URL} onUpdate={()=> window.scrollTo(0,0)}>
          <div className='wrapper'>
                <Header />
                <MainContent />
          </div>
        </Router>
      </>
     );
  }
}

const mapDispatchToProps = dispatch =>{
  return{
    setWidth: (isSpread)=> dispatch({type:'SPREAD_MENU',isSpread:isSpread})
  }
}

 
export default connect(null,mapDispatchToProps)(App);